html,
body,
#root {
  height: 100%;
  width: 100%;
  overflow: hidden;
  padding: 0;
  margin: 0;
}

body {
  font-family: 'Roboto', sans-serif;
  text-rendering: optimizeLegibility;
  font-feature-settings: 'kern';
  -webkit-font-feature-settings: 'kern';
  -moz-font-feature-settings: 'kern';
  -moz-font-feature-settings: 'kern=1';
}

